              </el-select>
            </el-form-item><el-form-item>
            <el-button type="primary" @click="onSubmit">查询</el-button>
          </el-form-item>
        </el-form>
        <div id="res">

          <ul id="arrli">
            <li v-for="(item,index) in data.content" :key="item.objId" @click="goMain(item.objId)">
