                <div class="form-top-left">
                  <div class="title"><span>{{item.logicalName}}</span></div>
                  <p class="description">
                    {{item.definition}}
                  </p>
                </div>
                <div class="form-top-right">
                  <div class="num-box">
                    <div class="num">2</div>
                    <div class="text">未阅读</div>
                  </div>
                  <div class="num-box">
                    <div class="num">2</div>
                    <div class="text">未阅读</div>
                  </div>
                  <div class="num-box">
