           @current-change="handleCurrentChange"
           :current-page="currentPage1"
           :page-size="100"
           layout="total, prev, pager, next"
           :total="1000"
           class="pagination"
           >
         </el-pagination>
       </div>


     </div>

   </el-col>

   <el-col :span="4">
    <div class="grid-content bg-purple"></div>
  </el-col>
</el-row>

</div>
</div>
</template>
<script>
  import axios from 'axios'
  export default {
    data(){
      return {
        currentPage1: 1,
        formInline:{
          user:'',
          region:''
        },
        data:{}
      }
    },
    mounted(){
      axios.get('192.168.1.135:18080/datablau-server/service/dataset/pages/0').then(res => {
        this.data = res.data;
        console.log(this.data);
      }).catch(error => console.log(error));

    },
    methods:{
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        console.log(`当前页: ${val}`);
      },
      onSubmit() {
        console.log('submit!');
      },
      goMain(id){
        this.$router.push({name:'dataMain',params:{id:id}});
      }
    }
  }
</script>
